.app-container {
    display: flex;
}

.left-panel {
    float: left;
    max-width: 20%;
    height: 100%;
}

.right-panel {
    /* width: auto;
    height: 100%;
    display: inline-block; */

    /* width: 100%; */
    /* height: 100vh; */
    /* display: inline-block; */

    flex: 1 1;
}

.map {
    width: 100%;
    height: 100vh;
}
.sidebar {
    height: 100%;
    width: 30%;
    position: fixed;
    z-index: 1;
    background-color: white;
    border-width: 1px;
    border-color: gray;
    border-style: solid;
}
.main-panel {
    width: 70%;
    margin-left: 30%;
    padding: 5px;
    background-color: red;
}

.trip-header-details-container{
    padding: 0px;
    height: 100px;
}

.trip-header-details{
    padding-left: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
}

.short-name {
    font-size: 1.6em;
    padding-top: 20px;
    margin: 0;
}

.long-name {
    margin: 0px;
    font-size: 1.2em;
}

.stop-containers-list {
    max-height: calc(100vh - 100px);
    overflow-y: auto;
}

.stop-info-container {
    padding-left: 10px;
    padding-right: 10px;
    border-top-width: thin;
    border-top-style: solid;
    border-top-color: #b6b6b6;
    overflow: hidden;
}

.stop-name {
    font-size: 1.1em;
    width: 70%;
    /* float: left; */
    display: inline-block;
    padding-top: 10px;
    padding-bottom: 10px;
}

.remaining-time {
    width: 30%;
    float: right;
    text-align: right;
    /* height: 100%; */
    padding-top: 10px;
    padding-bottom: 10px;
}

.remaining-time-value {
    font-size: 1.2em;
}

.remaining-time-unit {
    font-size: 0.9em;
}

.stop-interactions-container {
    overflow: hidden;
    max-height: 0px;
    -webkit-transition: max-height 1s; 
    transition: max-height 1s;  
    /*display: none; */
}

.stop-container:hover .stop-interactions-container {
    /*display: block;*/
    max-height: 50px;
}

.stop-interactions {
    padding-left: 10px;
    padding-right: 10px;
    background-color: #ffd752;
    padding-top: 4px;
    padding-bottom: 4px;
}

.stop-interaction-button {
    border-radius: 2px;
    border-width: thin;
    border-color: black;
    border-style: solid;
    padding: 2px 4px 2px 4px;
}

.stop-interaction-button {
    border-radius: 3px;
    border-width: thin;
    border-color: #dddddd;
    border-style: solid;
    padding: 2px 4px 2px 4px;
    background-color: #ffd752;
}

.stop-interaction-button-selected {
    background-color: #FFF176;
}
.popup-background {
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: #000000c2;
    display: flex;
    display: webkit-flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
}

.popup-container {
    z-index: 9999;
    width: 100%;
    text-align: center;
}

.popup {
    padding-left: 24px;
    padding-right: 0px;
    padding-top: 30px;
    padding-bottom: 8px;
    background-color: white;
    /* display: inline-block; */
    text-align: left;
    border-radius: 8px;
    /* max-height: 50vh; */
    max-width: 80vw;
    margin: auto;
}

@media screen and (min-width: 700px) {
    .popup {
        max-width: 50vw;
    }
}

.popup-header {
    margin-right: 24px;
}

.popup-header h3 {
    margin: 0;
    padding: 0;
}

.popup-contents {
    margin-right: 24px;
    overflow-y: auto;
    /* max-height: -webkit-fill-available; */
    max-height: 50vh;
    /* max-width: 70vw; */
    overflow-wrap: break-word;
    padding-right: 24px;
    height: 100%;
}

.popup-actions-container {
    margin-right: 8px;
    text-align: right;
    padding-right: 14px;
    padding-left: 14px;
    padding-top: 14px;
}

.popup-action-button {
    display: inline-block;
    padding-right: 10px;
    padding-left: 10px;
    padding-top: 8px;
    padding-bottom: 8px;
    display: inline-block;
    background: none;
    border-radius: 5px;
    border-width: 1px;
    border-color: gray;
    border-style: solid;
    cursor: pointer;

    -webkit-transition: all 0.3s cubic-bezier(0.22, 0.61, 0.36, 1);
    -webkit-transform: background-color;
    -ms-transition: all 0.3s cubic-bezier(0.22, 0.61, 0.36, 1);
    -ms-transform: background-color;
    -moz-transition: all 0.3s cubic-bezier(0.22, 0.61, 0.36, 1);
    -moz-transform: background-color;
    transition: all 0.3s cubic-bezier(0.22, 0.61, 0.36, 1);
}

.popup-action-button:hover {
    background-color: rgba(111, 111, 111, 0.51);
}

.tripInfo {
    display: inline-block;
